@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
.navbar {
  box-shadow: 0 2px 1px rgba(0, 0, 0, 0.08);
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 70px;
  padding: 0px 60px;
  width: 100%;
}

.text {
  font-weight: 800;
  font-size: 20px;
}

.dark-toggle {
  display: flex;
  width: 105px;
  justify-content: space-between;
  align-items: center;
}

.dark-toggle:hover {
  cursor: pointer;
}

.text-dark {
  font-weight: 600;
}


@media (min-width: 320px) and (max-width: 480px) {

  .navbar {
    padding: 0px 20px;
    height: 90px;
    width: 100%;
  }
  
  .text, .text-dark {
    font-size: 13px;
  }

  .dark-toggle {
    width: 30%;
  }
  
}

.filter-container {
  display: flex;
  justify-content: space-between;
}

.search-input-container {
  position: relative;
}

.search-icon {
  position: absolute;
  top: 17px;
  left: 30px;
}

.search-input {
  padding: 15px 5px 15px 70px;
  height: 48px;
  width: 400px;
  border: none;
  box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.05);
  outline: 0px;
  border-radius: 5px;
}

.dark-place::-webkit-input-placeholder{
  color: hsl(0, 0%, 100%);
}

.dark-place:-ms-input-placeholder{
  color: hsl(0, 0%, 100%);
}

.dark-place::-ms-input-placeholder{
  color: hsl(0, 0%, 100%);
}

.dark-place::placeholder{
  color: hsl(0, 0%, 100%);
}

.light-place::-webkit-input-placeholder{
  color: black;
}

.light-place:-ms-input-placeholder{
  color: black;
}

.light-place::-ms-input-placeholder{
  color: black;
}

.light-place::placeholder{
  color: black;
}
.select {
  width: 200px;
  padding: 15px;
  height: 48px;
  border: 0px;
  outline: 0px;
  box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.05);
  border-radius: 5px;
}

@media (min-width: 320px) and (max-width: 480px) {

  .filter-container {
    display: flex;
    flex-flow: column nowrap;
    height: 140px;
    width: 100%;
  }


  .search-input {
    width: 100%;
  }
}


.alert {
  border: 2px solid #F7CF4B;
  width: 100%;
  height: 60px;
  background-color: #FDF9CB;
  border-radius: 5px;
  padding: 15px;
}

.alert p{
  color: #DDB942;
  font-weight: 800;
}

.alert p > span {
  font-weight: 200;
}
.card-container {
  height: 380px;
  box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.05);
  border-radius: 5px;
  overflow: hidden;
}

.card-container:hover, .card-container:active {
  cursor: pointer;
  box-shadow: 20px 20px 20px rgba(0, 0, 0, 0.1);
  transform: translateY(5px);
}

.img {
  width: 100%;
  height: 220px;
}

.card-image {
  width: 100%;
  /* height: 100px; */
}

.card-image img{
  width: 100%;
}

.card-text {
  margin-left: 20px;
}

.card-text h3 {
  margin: 10px 0;
}

.card-text p {
  font-size: 13px;
  margin-bottom: 3px;
  font-weight: 600;
}

.light-span {
  color: hsl(0, 0%, 52%); 
  font-weight: 200;
}

.dark-span{
  color: white;
  font-weight: 300;
}

.btn-info {
  border-radius: 5px;
}

@media (min-width: 320px) and (max-width: 480px) {

  .card-container {
    width: 85%;
    height: 360px;
  }
  
  .img {
    width: 100%;
    height: 200px;
  }
}

.container {
  margin-top: 30px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  grid-template-rows: repeat(2, 380);
  grid-auto-rows: 380px;
  grid-gap: 50px;
}

.container-one {
  width: 300px ;
}

@media (min-width: 320px) and (max-width: 480px) {

  .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .container-one {
    width: 100% ;
  }
  
}

/* 
  ##Device = Tablets, Ipads (portrait)
  ##Screen = B/w 768px to 1024px
*/
@media (min-width: 481px) and (max-width: 767px) {
  
  /* CSS */
  .container {
    display: grid;
    grid-template-columns: repeat(2, minmax(280px, 1fr));
    grid-template-rows: repeat(2, 380);
    grid-auto-rows: 380px;
    grid-gap: 50px;
  } 

  .container-one {
    width: 100% ;
  }
}



.main-container {
  display: flex;
  flex-direction: column;
  max-width: 1440px;
  padding: 40px 70px;
  margin: auto;
}

.all {
  width: 100%;
  height: 100%;
}

.light-mode {
  background: hsl(0, 0%, 98%);
}

.dark-mode {
  background: hsl(209, 23%, 22%);
}

.dark-text {
  color: hsl(0, 0%, 100%);
}

.light-text {
  background: (200, 15%, 8%)
}


@media (min-width: 320px) and (max-width: 480px) {

  .main-container {
    padding: 20px 15px;
  }
  
}
.btn-back {
  padding: 5px 30px;
  display: flex;
  align-items: center;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border: none;
}

.btn-back:hover {
  cursor: pointer;
}

.btn-back span {
  margin-left: 8px;
}

.img-flag {
  width: 550px;
  height: 400px;
}

.flag-text-container {
  display: flex;
  justify-content: space-between;
  margin-top: 60px;
}

.text-section {
  margin: 40px  0px;
  width: 50%;
  font-weight: 600;
}

.information-text {
  margin-top: 40px;
  display: flex;
}

.information-text span {
  font-weight: 200;
  font-size: 14px;
}

.information-text p {
  margin-top: 8px;
}

.right-section-text {
  margin-left: 20%;
}

.border-countries-container {
  margin-top: 75px;
  display: flex;
  align-items: center;
}

.border-countries-container span{
  margin-right: 20px;
}

.border-countrie {
  padding: 5px 10px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  margin-right: 5px;
}
* {
  font-family: 'Nunito Sans', sans-serif;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.light-body {
  background: hsl(0, 0%, 98%);
}

.dark-body {
  background: hsl(207, 26%, 17%);
}
